
import { defineComponent } from '@vue/runtime-core';

export default defineComponent({
    name: 'Setting',
    data() {
        return {
            options: [{
                value: 0,
                label: 'Level 0'
            }, {
                value: 1,
                label: 'Level 1'
            }]
        }
    },
    computed: {
        currentLevel: {
            get() { return this.$store.state.currentLevel },
            set(val) { this.$store.commit("changeLevel", val) }
        }
    },
    mounted() {
    },
})
